	}
	.more{
		border: 1px solid #fca500;
		color: #fca500;
		width: 50%;
		margin: 10% auto;
		padding: 5% 10%;
		text-align: center;
	}
	/**/
	.all ul li,.asia1 ul li,.europe1 ul li,.north1 ul li,.oceania1 ul li,.africa1 ul li,.south1 ul li{
		background: #f7f7f7;
		margin-bottom: 10px;
		border-radius: 5px;
	}
	/**/
	.africa ul li:nth-child(3) img{
		height: 12vh;
	}
	/**/
	.south ul li:nth-child(5) img{
		height: 12vh;
	}
</style>



