		bottom: -100px;
		top: -200px;
		right: 0;
		background-color: rgba(0,0,0,.6);
	}
	.over .title{
		border-top-left-radius: 9px;
		border-top-right-radius: 9px;
		box-sizing: border-box;
		background: #ffdb00;
		text-align: center;
		padding: 10px 0;
		position: fixed;
		width: 100%;
	}
	.title>img{
		width: 20%;
	}
	.cont{
		margin: 50px 20px;
	}
	.cont>p{
		text-align: center;
		font-size: 18px;
		line-height: 50px;
	}
	.cont>div{
		margin-bottom: 20px;
	}
	.cont>div img{
		width: 3%;
		margin-right: 10px;
	}
	.cont>div>p{
		font-size: 18px;
		margin-bottom: 10px;
	}
	.cont>div>div{
		margin-left: 20px;
		color: gray;
		line-height: 22px;
	}
	.fini{
		background: #ffc900;
		text-align: center;
		border-top-left-radius: 20px;
		border-bottom-left-radius: 20px;
		border-top-right-radius: 20px;
		border-bottom-right-radius: 20px;
		position: fixed;
		bottom: 0px;
		left: 0;
		width: 95%;
		margin-left: 10px;
		line-height: 50px;
		z-index: 999999;
	}
</style>


